@import "https://fonts.googleapis.com/css2?family=Montserrat&display=swap";
@import "https://fonts.googleapis.com/css2?family=Fredoka+One&family=Montserrat&display=swap";
@import "https://fonts.googleapis.com/css2?family=Russo+One&display=swap";
@import "https://fonts.googleapis.com/css2?family=Lobster&display=swap";
@import "https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap";
html {
  min-height: 100%;
  position: relative;
}

h1 {
  text-align: center;
  color: #fff;
  font-family: Russo One, sans-serif;
  font-size: 32pt;
}

img {
  border-radius: 25px;
}

a {
  color: #fff;
  -webkit-text-decoration-line: none;
  text-decoration-line: none;
}

a:hover {
  color: #888;
}

.opcioActiva {
  color: #0ff;
}

.general {
  background-color: #000;
  flex-direction: column;
  margin: 0;
  font-family: montserrat;
  display: flex;
}

#cos {
  background-position: center;
  background-repeat: no-repeat;
}

header {
  z-index: 10;
  height: auto;
  min-width: 100%;
  text-align: center;
  background-color: #333;
  background-image: url("fonsCap.0e688d77.jpg");
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  display: flex;
  position: fixed;
  box-shadow: 0 8px 8px #00000026;
}

#titolCap {
  color: #fff;
  text-transform: uppercase;
  padding-left: 20px;
}

#barraMenu {
  color: #fff;
  border-radius: 2px;
  justify-content: center;
  font-weight: bold;
  display: flex;
}

#menu {
  flex-direction: row;
  padding: 10px;
  list-style: none;
  display: flex;
}

.opcio {
  padding-left: 10px;
  padding-right: 10px;
}

main {
  width: 100%;
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  justify-items: center;
  margin: 100px auto;
  display: flex;
}

#fotoGrup {
  max-height: 300px;
  box-shadow: 0 8px 20px 5px #00000026;
}

#textHistoria {
  padding: 30px;
  font-family: Bree Serif, serif;
  font-size: 1.5rem;
}

.containerFoto {
  background-image: url("2.4b36d964.jpg");
  justify-content: center;
  display: flex;
}

.containerHistoria {
  color: #bbb;
  background-image: url("1.bf7a8305.jpg");
  justify-content: center;
  font-size: 24px;
  display: flex;
}

.containerTemes {
  background-image: url("3.ae3cb238.png");
}

.containerDiscografia {
  background-image: url("4.c4fd30a9.jpg");
}

#discografia ul {
  flex-flow: wrap;
  justify-content: center;
  margin: 10px;
  padding: 10px;
  list-style: none;
  display: flex;
}

#discografia ul li {
  height: 150px;
  width: 150px;
  opacity: .8;
  background-image: url("discVinil_trans.6c4d1a08.png");
  background-position: center;
  background-size: contain;
  margin-top: 10px;
  margin-left: 10px;
}

#discografia ul li h1 {
  color: #900;
  text-shadow: 0 0 1px #fff;
  height: 50px;
  margin-top: 55px;
  font-family: Russo One, sans-serif;
  font-size: 16px;
}

#discografia ul li p {
  text-align: center;
  color: #fff;
  font-family: montserrat;
  font-size: 12px;
}

.contingut {
  max-width: 950px;
  margin: auto;
  padding: 10px;
}

.containerSongs {
  flex-flow: wrap;
  justify-content: center;
  padding: 10px;
  display: flex;
}

.song {
  width: 350px;
  background-color: #555;
  border-radius: 30px;
  flex-direction: column;
  margin: 10px;
  padding: 5px;
  display: flex;
}

.song .videoSong {
  max-width: 350px;
  align-items: center;
  margin: 10px auto;
}

.dadesSong {
  vertical-align: text-top;
  max-width: 350px;
  height: 100%;
  padding: 10px;
}

.dadesSong ul li {
  color: bisque;
  margin-top: 5px;
  font-size: .9rem;
  list-style-type: none;
}

.dadesSong ul li p {
  margin: 5px 0 0;
}

.dadesSong ul li a {
  color: bisque;
}

.titolSong {
  color: #fff;
  text-align: left;
  font-size: 1rem;
}

footer {
  color: #fff;
  min-width: 100%;
  background-color: #333;
  background-image: url("fonsCap.0e688d77.jpg");
  flex-direction: row;
  justify-content: space-around;
  font-family: Montserrat, sans-serif;
  display: flex;
  position: absolute;
  bottom: 0;
  box-shadow: 0 -8px 8px #00000026;
}

footer div {
  text-align: center;
  flex: auto;
  padding-right: 20px;
}

@media screen and (max-width: 700px) {
  header {
    height: 150px;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
  }

  #titolCap {
    color: #fff;
    text-transform: uppercase;
    padding: 0;
  }

  #menu {
    flex-direction: row;
    margin: 0;
    padding: 0;
  }

  #fotoGrup {
    width: 100%;
    box-shadow: 0 8px 20px 5px #00000026;
  }

  #textHistoria {
    padding: 0;
  }

  #containerCat {
    width: 100%;
    flex-direction: row;
    justify-content: center;
    margin: 0;
  }

  .puntCat {
    width: 42%;
    margin-bottom: 10px;
    margin-left: 0;
    margin-right: 5px;
  }

  .puntActiu {
    width: 85%;
    margin-bottom: 10px;
    margin-left: 5px;
    margin-right: 5px;
  }

  .miniatura {
    width: 90%;
    height: 100px;
    object-fit: cover;
  }

  main {
    margin-top: 150px;
    margin-bottom: 250px;
  }

  footer {
    flex-direction: column;
  }
}

